@import url(https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500,500i,700,700i,900&display=swap);
@font-face{font-family:"Alfa Slab One";src:url(/fonts/Alfa_Slab_One/AlfaSlabOne-Regular.ttf)}body{margin:0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.inherit-button{cursor:pointer;border:0;color:inherit;background:none;padding:0px}.hidden{display:none}

.video-list-item{-webkit-transition:all 0.2s ease-out;transition:all 0.2s ease-out;background-color:transparent;border:0px;cursor:pointer}.video-list-item .card-header-container{position:relative}.video-list-item .card-header-container .card-title{font-family:Alfa Slab One;font-size:0.9em}.video-list-item .card-header-container .card-img-overlay{color:#50e3c2;background:rgba(0,0,0,0.3);top:inherit;padding:8px}.video-list-item .card-sub-title,.video-list-item .card-text{color:white;font-size:0.8em}.video-list-item .card-text{color:gray}.video-list-item .card-body{padding:0}.video-list-item .play-button img{max-width:30px}

.scrollable-container{position:fixed;overflow:scroll;top:0;padding-top:120px;bottom:0;left:0;right:0}

.video-details-component .video-details-title{font-family:Alfa Slab One;color:#50e3c2;font-size:0.9em}.video-details-component .video-details-description{color:gray;font-size:0.8em}.video-details-component .video-deatails-published{color:white;font-size:0.9em}.video-details-component .video-details-back{color:#50e3c2;font-size:0.8em}

@-webkit-keyframes loadAnimationHeader{from{background-position-x:-60px}to{background-position-x:0px}}@keyframes loadAnimationHeader{from{background-position-x:-60px}to{background-position-x:0px}}.header-component{-webkit-transition:all 0.2s ease-out;transition:all 0.2s ease-out;background-position:0 0;height:40px;-webkit-animation:loadAnimationHeader 1.3s normal forwards ease-in-out;animation:loadAnimationHeader 1.3s normal forwards ease-in-out}.header-component .navbar-brand{margin-top:4px}

@-webkit-keyframes loadAnimation{from{background-position-y:-1200px}to{background-position-y:0px}}@keyframes loadAnimation{from{background-position-y:-1200px}to{background-position-y:0px}}.dashboard-page{-webkit-transition:all 0.2s ease-out;transition:all 0.2s ease-out;background-color:#494848;background-size:cover;background-position:top left;position:absolute;float:left;top:0;right:0;bottom:0;left:0;-webkit-animation:loadAnimation 1s normal forwards ease-in-out;animation:loadAnimation 1s normal forwards ease-in-out}

